export default class Watcher {
    constructor() {
        this.st = new Set();
    }
    register(handler) {
        this.st.add(handler);
    }
    unregister(handler) {
        this.st.delete(handler);
    }
    notify(...args) {
        for (const handler of this.st) {
            handler(...args);
        }
    }
    get DEBUG_stSize() {
        return this.st.size;
    }
}
